<template>
    <div class="login-container">
        <p class="login-title">이메일 인증완료</p>
        <div style="padding:150px;text-align: center;font-size:18px;">
            이메일 인증을 완료하였습니다<br>
            회원가입을 축하합니다

        </div>
        <div class="button-row">
            <v-btn type="button" @click="$router.push('/memberLogin')" class="blackButton">
                로그인 하기
            </v-btn>
        </div>


    </div>
</template>
<script>
    import apiService from '@/config/web/apiService'
    import {
        API_URL_UPDATE_EMAIL_AUTH
    } from '@/config/web/urls'
    import AUTH from '@/config/web/auth'

    export default {
        name: 'emailAuth',
        data() {
            return {
                insertFlag: false,

            };
        },
        created() {
            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                this.$router.push('/');
                return;
            }

            this.$eventBus.$emit('overlay', 'open');
            this.setAuth()

        },
        methods: {
            setAuth() {
                //accept=%242a%2410%24Mlh5P0Z12LpGeL6lQQ5AE.PkE0V4WQcdvFWDvMo%2FHG9go7Ez32nqu
                const frm = {
                    code: this.$route.params.code
                }
                if (this.insertFlag) return;
                this.insertFlag = true;

                apiService.post(API_URL_UPDATE_EMAIL_AUTH, frm, (res) => {
                    let result = res.status;
                    if (result == 'success') {
                        this.$eventBus.$emit('overlay', 'hide');

                    }

                })

            },

        }

    }
</script>

<style lang="scss" scoped>
    .login-container {
        width: 636px;
        margin: 0 auto;
        padding: 36px 24px;
        min-height: calc(100vh - 250px);
        background: #fff;

        .login-title {
            color: #333;
            font-size: 24px;
            font-weight: bold;
            margin-bottom: 35px;
            padding-bottom: 8px;
            border-bottom: solid 1px #333;
        }

        .w-full {
            width: 100%;
            margin: auto;

            .input-box {
                .label {
                    font-size: 14px;
                    color: #333;
                    padding-bottom: 5px;
                    font-weight: 600;
                }

                width: 100%;
                margin: auto;
            }
        }

        .button-row {
            width: 50%;
            margin: auto;
            padding: 10px 0 25px 0;

            .join-btn {
                color: #333;
                font-size: 14px;
                text-decoration: underline;
                cursor: pointer;
            }

            .guide {
                color: #999999;
                font-size: 14px;
                padding-right: 20px;
            }
        }

        .sub-title {
            color: #232323;
            font-weight: 600;
            font-size: 16px;
            text-align: center;
        }

        .find-box {
            padding-bottom: 15px;

            .right {
                text-align: center;
                color: #999999;
                font-size: 14px;
                cursor: pointer;
                text-decoration: underline;
            }
        }

        .divide-line {
            width: 100%;
            margin: 20px 0;
            border: 1px solid #DCDCDC;
            height: 0;
        }

        .sns-box {
            .sns-ul {
                padding: 0;
                margin: 0;

                li {
                    cursor: pointer;
                    width: 100%;
                    height: 48px;
                    border: solid 1px #dcdcdc;
                    border-radius: 5px;
                    margin: 10px 0;
                    text-align: center;
                    line-height: 48px;

                    img {
                        margin: 8px 10px 0 0;
                    }

                    span {
                        position: relative;
                        top: -8px;
                        font-size: 18px;
                        color: #333;
                        font-weight: bold;

                    }
                }
            }
        }
    }

    @media screen and (min-width: 450px) and (max-width: 1259px) {
        .login-container {
            width: 90%;
            margin: 40px auto;
        }

    }

    @media screen and (max-width: 450px) {
        .login-container {
            width: 100%;
            margin: 0;
            padding: 36px 20px;
        }
    }
</style>
